/* App.module.css */
.container {
  font-family: Arial, sans-serif;
  padding: 20px;
  background-color: #f5f5f5;
  color: #333;
}

.header {
  text-align: center;
  margin-bottom: 40px;
  background-color: #ffffff; /* Light red */
  padding: 20px;
  border-radius: 8px;
}

.header h1 {
  font-size: 3em;
  font-weight: bold;
  margin-bottom: 10px;
  color: #333;
}

.header p {
  font-size: 1.2em;
  color: #666;
}

.section {
  margin-bottom: 40px;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.section:nth-of-type(odd) {
  background-color: #ededed; /* Light green */
}

.section:nth-of-type(even) {
  background-color: #add8e6; /* Light blue */
}

.section h2 {
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 10px;
  color: #333;
}

.section p {
  font-size: 1.2em;
  margin-bottom: 20px;
  color: #666;
}